/* Main Content Section*/

.body-container {
    display: grid;
    /* grid-template-columns: 45vw 1fr; */
    /* grid-template-rows: 500px 365px 1fr; */
    /* grid-template-rows: 53.5% 50% 1fr; */
    /* grid-template-rows: 53.82% 39.29% 6.89%; */
    grid-template-columns: 45% 1fr;
    grid-template-rows: 500px 39.3vh 1fr;
    /* grid-template-rows: 60% 36.1% 1fr; */
    /* grid-template-rows: 55% 40% 1fr; */
    background-color: #1A1B21;
}

.main-img {
    /* width: 44.6vw;
    height: 33.25rem; */
    width: 44.6vw;
    /* height: 27.7vw; */
    height: 100%;
}

.content {
    margin: auto;
    text-align: center;
}

.content h1 {
    color: #FFFFFF;
    padding: 0 0 3.153vw;
}

.content h6 {
    color: #F3BF99;
    padding: 0 0 2.627vw;
}

.content small {
    color: aliceblue;
    opacity: 80%;
}

a {
    text-decoration: none;
    display: block;
}

.web-link {
    color: #f5f5f5;
    width: fit-content;
    margin: auto;
}

.content small:hover {
    color: #F3BF99;
}

.btn {
    margin: 1.875rem 0.938rem 1.875rem 0.938rem;
    width: 7.813rem;
}

.btn span {
    padding: 0 0.625rem 0;
}

.btn-envelope {
    background-color: #D1D5DB;
}

.btn-envelope a {
    color: #1A1B21;
}

.btn-linkedin {
    background-color: #5093E2;
}

.btn-linkedin a {
    color: #FFFFFF;
}

/* About Section */

.about {
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
    text-align: center;
    grid-column-start: 1;
    grid-column-end: 3;
}

.about h5 {
    /* margin: 3.5rem auto 2rem auto; */
    /* margin: 3% auto 2% auto; */
    margin: 5vh auto 5vh;
    color: #1A1B21;
}

/* Footer Section */

.footer {
    text-align: center;
    margin: auto auto 0;
    width: 100%;
    grid-column-start: 1;
    grid-column-end: 3;
    background-color: #161619;
}

.footer a {
    display: inline-block;
    padding: 1.25rem;
    color: #918E9B;
}

/* For small devices */

@media (min-width:768px) and (max-width:1024px) {
    body{
        background-color: #1A1B21;
    }
    .body-container {
        /* display: flex;
        flex-direction: column; */
        grid-template-columns: 1fr;
        grid-template-rows: 40% 30% 40% auto;
        /* grid-template-rows: auto auto auto auto; */
        /* grid-auto-flow:row; */
        /* grid-template-rows: 1fr 40vw auto auto; */
        /* height: 100%; */
    }

    .main-img {
        width: 100%;
        /* height: 40vh; */
        /* height: 50vh; */
        height: 100%;
    }

    .content {
        grid-row-start: 2;
        grid-row-end: 3;
        /* margin: 20vw auto; */
        margin: 200px auto;
    }

    /* h1,h2,h3,h4,h5,h6,small{
        font-size: 3rem;
    } */

    .about {
        /* background-color: #1A1B21; */
        background-color: #1A1B21;
        text-align: center;
        /* margin: 0 12vw; */
        margin: 0 auto;
    }

    .about h5 {
        /* margin: 1.051vw 0;
        padding: 0.788vw 0; */
        margin: 1.051vw 0;
        padding: 0.788vw 0;
        color: #f5f5f5;
        text-align: center;
    }

    .about small {
        color: aliceblue;
        margin-bottom: 2vw;
        opacity: 80%;
    }
    .footer{
    }
}